@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
  font-family: 'Poppins', sans-serif;
}

.content {
  margin-left: 20px;
  margin-right: 20px;
}

.content * {
  margin-bottom: 10px;
}
h1 {
    margin:0;
    padding: 10px;
    background-color: black;
    color:white;
}
.username-form {
    text-align: left;
}

.form-inline{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-align-items: center;
            align-items: center;
}
label {
    display: block;
    text-align: left;
    padding-right: 15px;
    color: #404040;
}

input {
    display: block;
    border: 1px solid #cdcdcd;
    border-radius: 5px;
    height: 25px;
    margin-right: 8px;
}

button {
    font-family: 'Poppins';
    background-color: black;
    color:white;
    border:grey 1px solid;
    border-radius: 5px;
    height:28px;

}


.metrics-display__overview {
    margin:auto;
    width:80%;
    border: 1px solid lightgray;
}

.user__profile-photo{
    margin-top:32px;
}

img {
    border-radius:50%;
    width:100px;
}

.bold {
    font-size:24px;
    font-weight:700;
}
.metrics-display__table-container{
    margin-top:32px;
}

table {
    border-collapse: collapse;
    margin-left:auto;
    margin-right:auto;
    width:80%;
}

table tr {
    border: 1px solid darkgray;
}

table tr th {
    padding: 8px;
    border: 1px solid darkgray;
    background-color: #cdcdcd;
}

table tr td {
    padding: 8px;
    border: 1px solid darkgray;
}

.metrics-display-fineprint{
    font-size: smaller;
}

em {
    color: #9c9c9c;
}
